<script>
import { Form, FormItem, Input, Button } from 'element-ui';
export default {
    components: {
        elForm: Form,
        elFormItem: FormItem,
        elInput: Input,
        elButton: Button,
    },
    name: 'LoginView',
    data() {
        return {
            loading: false,
            postData: {
                account: '18974103150',
                password: '123456',
            },
            loginRules: {
                account: [
                    { required: true, message: '请输入手机号', trigger: 'blur' },
                    { min: 11, max: 11, message: '请输入符合中国大陆的手机号', trigger: 'blur' },
                ],
                password: [{ required: true, message: '请输入登录密码', trigger: 'blur' }],
            },
        };
    },
    methods: {
        loginHandle(formName) {
            const thisObj = this;
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    thisObj.loading = true;
                    thisObj.$store
                        .dispatch('user/login', thisObj.postData)
                        .then(() => {
                            this.$router.push({
                                path: this.redirect || '/',
                                query: this.otherQuery,
                            });
                            this.loading = false;
                        })
                        .catch(() => {
                            this.loading = false;
                        });
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
    },
};
</script>

<template>
    <div class="container">
        <!-- 毛玻璃背景 -->
        <img class="frosted-glass" src="@images/login_bg.png" alt="毛玻璃背景" />
        <!-- Logo -->
        <img class="logo" src="@images/logo.png" alt="Logo图片" />
        <!-- 登录框 -->
        <el-form class="loginForm" :model="postData" :rules="loginRules" ref="loginForm">
            <h4>账号登录</h4>
            <el-form-item prop="account">
                <el-input
                    class="loginForm-input"
                    type="text"
                    v-model="postData.account"
                    placeholder="请输入手机号"
                    maxlength="11"
                ></el-input>
            </el-form-item>
            <el-form-item prop="password">
                <el-input
                    class="loginForm-input"
                    type="password"
                    v-model="postData.password"
                    placeholder="请输入登录密码"
                ></el-input>
            </el-form-item>
            <el-form-item>
                <el-button
                    :loading="loading"
                    class="submitBtn"
                    type="primary"
                    @click="loginHandle('loginForm')"
                    >登录</el-button
                >
            </el-form-item>
        </el-form>
    </div>
</template>

