        height: 119px;
    }

    .loginForm {
        z-index: 2;
        box-sizing: border-box;
        margin: 16px auto 0 auto;
        padding: 0 45px;
        border-radius: 10px;
        width: 620px;
        height: 500px;
        background: #fff;
        box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.1);
        ::v-deep .loginForm-input {
            input {
                display: block;
                // margin: 47px auto 0 auto;
                margin: 32px auto 0 auto;
                border: 0;
                border-radius: 10px;
                padding: 0 23px;
                // width: 530px;
                width: 100%;
                height: 66px;
                font-family: 'Inter';
                font-size: 25px;
                font-weight: 400;
                font-style: normal;
                line-height: 30px;

                // text-align: center;
                letter-spacing: 0.02em;
                color: #000;
                background: #e8f0fe;
                box-shadow: inset 0 1px 4px rgba(0, 0, 0, 0.1);
                &:focus {
                    outline: none;
                }
                &::-webkit-input-placeholder {
                    /* WebKit browsers，webkit内核浏览器 */
                    color: #bcbcbc;
